.header {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 6rem;
    width: 90vw;
    border-bottom: 0.15rem solid lightslategray;
}

.logo {
    width: 6rem;
    margin-left: 3rem;
}

.header__content {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
}

.btn {
    background-color: #FFD700;
    border-radius: 8px;
    padding: 0.5rem 1rem;
}